import React, { Component, Suspense, lazy } from "react";
import { Link } from "react-router-dom";
import FormPembayaranPerBon from "./Component/FormPembayaranPerBon";
import { connect } from "react-redux";
import {
  setTampungCiok,
  settampungRongsok,
} from "../actions/pembayaranSupplierAction";
import {
  setCashTemp,
  setGiroTemp,
  setTransferTemp,
} from "../actions/customerAction";
import EditComponent from "../Stocking/Component/EditComponent";
import Axios from "axios";
import Swal from "sweetalert2";
import { getUUID } from "../actions/fakturAction";
import Skeleton from "react-loading-skeleton";
import HeaderCard from "../HeaderCard";

const FormRongsok = lazy(() => import("./Component/FormRongsok"));
const FormCiok = lazy(() => import("./Component/FormCiok"));
const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const maptoState = (state) => {
  return {
    pembayaranSupplierList: state.provinsi.pembayaranSupplier,
    tampungCiok: state.provinsi.tampungCiok,
    tampungRongsok: state.provinsi.tampungRongsok,
    tampungCash: state.provinsi.tampungCash,
    tampungTransfer: state.provinsi.tampungTransfer,
    tampungGiro: state.provinsi.tampungGiro,
    totalCiok: state.provinsi.totalCiok,
    totalBrutoCiok: state.provinsi.totalBrutoCiok,
    totalRongsok: state.provinsi.totalRongsok,
    totalBrutoRongsok: state.provinsi.totalBrutoRongsok,
    sumTunai: state.provinsi.sumTunai,
    sumTunaiNetto: state.provinsi.sumNetto,
    sumTransfer: state.provinsi.sumTransfer,
    sumTransferNetto: state.provinsi.sumTransferNetto,
    sumGiro: state.provinsi.sumGiro,
    sumGiroNetto: state.provinsi.sumGiroNetto,
    selectedBon: state.provinsi.selectedBon,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
  };
};
class PembayaranPerBon extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    this.props.dispatch(getUUID());
    this.props.dispatch(setTampungCiok());
    this.props.dispatch(settampungRongsok());
    this.props.dispatch(setCashTemp());
    this.props.dispatch(setTransferTemp());
    this.props.dispatch(setGiroTemp());
  }

  handleSubmit(data) {
    console.log(data);
    var bayarBarang = JSON.parse(localStorage.getItem("alatBayarBarang"));
    var bayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf"));
    var bayarTunai = JSON.parse(localStorage.getItem("alatBayarTunai"));
    var bayarGiro = JSON.parse(localStorage.getItem("alatBayarGiro"));
    var split = data.supplier ? data.supplier : "DEFAULT | DEFUALT";
    var supplier = split.split("|");
    var isi = {
      alatBayarBarang: bayarBarang,
      alatBayarTunai: bayarTunai,
      alatBayarTrf: bayarTrf,
      alatBayarGiro: bayarGiro,
      totalFakutrTrx: null,
      keterangan: data.keterangan,
      totalAlatBayar: {
        totalRsk: this.props.totalRongsok,
        totalCt: this.props.totalCiok,
        totalTunaiRp: localStorage.getItem("TotalTunai"),
        totalTrfRp: localStorage.getItem("totalTransfer"),
        totalPutusGr: 0,
        tipeData: "BON GR",
        totalPutusRp: 0,
      },
      kodeSupplier: supplier[0],
      namaSupplier: supplier[1],
      noBon: data.noBon,
      noFaktur: this.props.noFaktur,
      tanggal: data.tanggal,
      namaToko: data.namaToko,
      kodeCustomer: data.kodeCustomer,
      namaCustomer: data.namaCustomer,
      tipeTransaksi: "",
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(isi);
    Axios.post(server + "cst/dc/byr", isi)
      .then(() =>
        Swal.fire({
          position: "top-right",
          icon: "success",
          text: "Transaksi Berhasil",
          timer: 2500,
          showConfirmButton: false,
        })
      )
      .catch((err) =>
        Swal.fire({
          position: "top-right",
          icon: "error",
          text: "Sepertinya terjadi kesalahan, silahkan check koneksi anda",
          timer: 2500,
          showConfirmButton: false,
        }).then(() => console.log(err))
      );
  }
  handleSubmitCiok(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kodeBarang: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadarTransaksi: data.kadarTransaksi,
    };
    var ciokTemp = JSON.parse(localStorage.getItem("ciokTemp")) || [];
    ciokTemp.push(isi);
    localStorage.setItem("ciokTemp", JSON.stringify(ciokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarBarang", JSON.stringify(alatBayarBarang));
    this.props.dispatch(setTampungCiok());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  handleSubmitRongsok(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kodeBarang: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadarTransaksi: data.kadarTransaksi,
    };
    var rongsokTemp = JSON.parse(localStorage.getItem("rongsokTemp")) || [];
    rongsokTemp.push(isi);
    localStorage.setItem("rongsokTemp", JSON.stringify(rongsokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarBarang", JSON.stringify(alatBayarBarang));
    this.props.dispatch(settampungRongsok());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-d-12">
          <ol className="breadcrumb mb-2">
            <Link to="/KlasifikasiCustomer">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Pembayaran Per Bon" />
            <div className="card-body">
              <div className="row">
                <div className="col-lg-9">
                  <h1>Pembayaran Per Bon</h1>
                </div>
                <div className="col-lg-3">
                  <div className="text-right">
                    <button
                      className="btn btn-warning"
                      data-target="#editModal"
                      data-toggle="modal"
                    >
                      Edit Pembayaran Per Bon
                    </button>
                  </div>
                </div>
                <div className="col-lg-12">
                  <br />
                </div>
              </div>
              <FormPembayaranPerBon
                noFaktur={this.props.noFaktur}
                selectedData={this.props.selectedBon}
                tampungCiok={this.props.tampungCiok}
                tampungRongsok={this.props.tampungRongsok}
                totalCiok={this.props.totalCiok}
                totalBrutoCiok={this.props.totalBrutoCiok}
                totalBrutoRongsok={this.props.totalBrutoRongsok}
                totalRongsok={this.props.totalRongsok}
                tampungCash={this.props.tampungCash}
                tampungTransfer={this.props.tampungTransfer}
                tampungGiro={this.props.tampungGiro}
                sumTunai={this.props.sumTunai}
                sumTunaiNetto={this.props.sumTunaiNetto}
                sumTransfer={this.props.sumTransfer}
                sumTransferNetto={this.props.sumTransferNetto}
                sumGiro={this.props.sumGiro}
                sumGiroNetto={this.props.sumGiroNetto}
                username={this.props.username}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Giro
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <EditComponent
                  onSubmit={(data) => this.handleSubmitGiro(data)}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="ciokModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Ciok
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={421} />
                    </div>
                  }
                >
                  <FormCiok
                    data={this.props.tampungCiok}
                    onSubmit={(data) => this.handleSubmitCiok(data)}
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="rongsokModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Rongsok
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={420} />
                    </div>
                  }
                >
                  <FormRongsok
                    onSubmit={(data) => this.handleSubmitRongsok(data)}
                    data={this.props.tampungRongsok}
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(PembayaranPerBon);
