import React, { Component, Suspense, lazy } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { setSelectedBon } from "../../actions/customerAction";
import Skeleton from "react-loading-skeleton";
import Swal from "sweetalert2";
import Stepper from "react-stepper-horizontal/lib/Stepper";
import SelectSearch from "react-select-search";
import Axios from "axios";
import { gagal, warning } from "../../Alert";
import { getUUID } from "../../actions/fakturAction";
const FormBayarPerBon = lazy(() => import("../Element/FormBayarPerBon"));

const { SearchBar } = Search;
const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class FormPembayaranPerBon extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDateNow = `${date.getFullYear()}-${date.getMonth() + 1
      }-${date.getDate()}`;
    var formatedDate = `${date.getFullYear()}${date.getMonth() + 1
      }${date.getDate()}`;
    this.state = {
      date: formatedDate,
      dateNow: formatedDateNow,
      urutan: 0,
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      namaToko: "",
      noBon: "",
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "namaAktor",
          text: "Nama Toko",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "bruto",
          text: "bruto (Gr)",
          headerStyle: () => {
            return { width: "9%" };
          },
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          headerStyle: () => {
            return { width: "9%" };
          },
        },
        {
          dataField: "rp",
          text: "ongkos (Rp)",
          headerStyle: () => {
            return { width: "9%" };
          },
        },
        {
          dataField: "aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  className="btn btn-success"
                  onClick={(e) =>
                    this.pilihBon(
                      row.noFaktur,
                      row.tanggal,
                      row.namaAktor,
                      row.noBon,
                      row.bruto,
                      row.netto,
                      row.rp,
                      row.statusValid,
                      e,
                      this.disabledButton(e)
                    )
                  }
                >
                  <i className="fa fa-check-circle mr-2"></i>
                  PILIH
                </button>
              </div>
            );
          },
          headerStyle: () => {
            return { width: "9%" };
          },
        },
      ],
      pilihanColumns: [
        {
          dataField: "Tanggal",
          text: "Tanggal",
          headerStyle: () => {
            return { width: "10%" };
          },
          footer: " ",
        },
        {
          dataField: "namaAktor",
          text: "Nama Toko",
          headerStyle: () => {
            return { width: "10%" };
          },
          footer: " ",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
          headerStyle: () => {
            return { width: "10%" };
          },
          footer: " ",
        },
        {
          dataField: "bruto",
          text: "Bruto (Gr)",
          headerStyle: () => {
            return { width: "9%" };
          },
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          headerStyle: () => {
            return { width: "9%" };
          },
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
        {
          dataField: "ongkos",
          text: "Ongkos (Rp)",
          headerStyle: () => {
            return { width: "9%" };
          },
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
        {
          dataField: "aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowIndex, row, columnIndex) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  data-toggle="modal"
                  data-target="#editModal"
                  className="btn btn-danger"
                  onClick={() => this.deleteArray(columnIndex)}
                >
                  <i className="fa fa-trash mr-2"></i>
                  Hapus
                </button>
              </div>
            );
          },
          headerStyle: () => {
            return { width: "9%" };
          },
        },
      ],
      tampungData: [
        {
          tanggal: "20/10/2020",
          namaToko: "Octa Gold",
          noBon: "BN-00001",
          sales: "Sales 2",
          bonGr: "200",
          bonRp: "100",
          titipGr: "200",
          titipRp: "100",
        },
        {
          tanggal: "20/10/2020",
          namaToko: "Octa Gold",
          noBon: "BN-00002",
          sales: "Sales 2",
          bonGr: "300",
          bonRp: "200",
          titipGr: "300",
          titipRp: "200",
        },
      ],
      selectedData: [
        {
          tanggal: "Pilihan",
        },
      ],
      listCustomer: [],
      listBarang: [],
    };
  }

  deleteArray(index) {
    var isi = JSON.parse(localStorage.getItem("selectedData")) || [];
    isi.splice(index, 1);
    localStorage.setItem("selectedData", JSON.stringify(isi));
    this.props.dispatch(setSelectedBon());
  }
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    } else if (step === 1) {
      this.setState({ step: step + 1, step2: "row d-none", step3: "row" });
    }
    this.props.change("namaToko", this.state.namaToko);
    this.props.change("tanggal", this.state.dateNow);
  };
  nextStep1 = () => {
    const { step } = this.state;
    if (this.state.namaToko === "") {
      Swal.fire({
        icon: "error",
        text: "Silahkan Isi Nama Toko",
        showConfirmButton: false,
        timer: 1500,
      }).then(() => {
        return false;
      });
    } else {
      localStorage.setItem("namaToko", this.state.namaToko);
      localStorage.setItem("noBon", this.state.noBon);
      if (step === 0) {
        this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
      } else if (step === 1) {
        this.setState({ step: step + 1, step2: "row d-none", step3: "row" });
      }
    }
  };

  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    } else if (step === 2) {
      this.setState({ step: step - 1, step3: "row d-none", step2: "row" });
    }
  };

  pilihBon = (
    noFaktur,
    tanggal,
    namaAktor,
    noBon,
    bruto,
    netto,
    ongkos,
    status,
    e
  ) => {
    var data = {
      noFaktur: noFaktur,
      Tanggal: tanggal,
      namaAktor: namaAktor,
      noBon: noBon,
      bruto: bruto,
      netto: netto,
      ongkos: ongkos,
    };
    if (status === "OPEN") {
      gagal("Faktur belum Divalidasi, Mohon Validasi Dahulu");
      return false;
    } else {
      // this.disabledButton(e);
      var isi = JSON.parse(localStorage.getItem("selectedData")) || [];
      var check = isi.findIndex(data => data.noFaktur === noFaktur)
      if(check < 0){
        isi.push(data);
      localStorage.setItem("selectedData", JSON.stringify(isi));
      console.log("Berhasil Ditambahkan");
      this.setState({
        urutan: this.state.urutan + 1,
      });
      this.props.dispatch(setSelectedBon());
      Swal.fire({
        position: "top-end",
        icon: "success",
        text: "Data Berhasil Disimpan!",
        showConfirmButton: false,
        timer: 1500,
      });
      } else {
        gagal("Faktur Sudah Terpilih")
      }
    }
  };
  disabledButton = (e) => {
    e.target.disabled = true;
  };

  componentDidMount() {
    this.props.dispatch(setSelectedBon());
    this.props.change("tanggal", this.state.tanggal);
    this.props.change("totalBayar", this.props.totalBayar);
    this.props.dispatch(getUUID())
    Axios.get(server + "customers").then((res) =>
      this.setState({
        listCustomer: res.data,
      })
    );
  }

  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  PilihToko(data) {
    var split = data.split("|");
    this.setState({
      namaToko: split[0],
      namaCustomer: split[1],
      kodeCustomer: split[2],
    });
    this.props.change("namaToko", split[0]);
    this.props.change("namaCustomer", split[1]);
    this.props.change("kodeCustomer", split[2]);
  }
  handleSubmitRp(data) {
    if(data.namaToko === undefined){
      warning("Mohon Pilih Toko")
      return false
    } else if(data.lokasi === undefined){
      warning("Mohon Pilih Lokasi")
      return false
    }
    console.log("MASUK SUBMIT RP");
    var simpan = {
      alatBayarBarang: JSON.parse(localStorage.getItem("alatBayarBarang")),
      alatBayarTunai: JSON.parse(localStorage.getItem("alatBayarTunai")),
      alatBayarTrf: JSON.parse(localStorage.getItem("alatBayarTrf")),
      alatBayarGiro: null,
      footerDocument: {
        inputBy: this.props.username,
      },
      kodeCustomer: data.kodeCustomer,
      namaToko: data.namaToko,
      noFaktur: this.props.noFaktur,
      kodeLokasi: data.lokasi,
      tanggal: data.tanggal,
      noBon: this.state.noBon,
      keterangan: "-",
      tipeTransaksi: this.state.tipeTransaksi,
      totalAlatBayar: {
        totalTrfRp: 0,
        totalTunaiRp: 0,
        totalCt: 0,
        totalRsk: 0,
        totalGiroRp: 0,
        totalPutusRp: 0,
        totalPutusGr: 0,
        tipeData: "RUPIAH",
      },
      totalFakutrTrx: null,
      namaSales: "-",
    };
    console.log(simpan);
  }
  handleSubmitGr(data) {
    if(data.namaToko === undefined){
      warning("Mohon Pilih Toko")
      return false
    } else if(data.lokasi === undefined){
      warning("Mohon Pilih Lokasi")
      return false
    }
    console.log("MASUK SUBMIT RP");
    var simpan = {
      alatBayarBarang: null,
      alatBayarTunai: JSON.parse(localStorage.getItem("alatBayarTunaiRp")),
      alatBayarTrf: JSON.parse(localStorage.getItem("alatBayarTrfRp")),
      alatBayarGiro: null,
      footerDocument: {
        inputBy: this.props.username,
      },
      kodeCustomer: data.kodeCustomer,
      namaToko: data.namaToko,
      noFaktur: this.props.noFaktur,
      kodeLokasi: data.lokasi,
      tanggal: data.tanggal,
      noBon: this.state.noBon,
      keterangan: "-",
      tipeTransaksi: this.state.tipeTransaksi,
      totalAlatBayar: {
        totalTrfRp: 0,
        totalTunaiRp: 0,
        totalCt: 0,
        totalRsk: 0,
        totalGiroRp: 0,
        totalPutusRp: 0,
        totalPutusGr: 0,
        tipeData: "RUPIAH",
      },
      totalFakutrTrx: null,
      namaSales: "-",
    };
    console.log(simpan);
  }

  setTipeTransaksi(e) {
    console.log("MASUK SET TIPE TRANSAKSI - " + e.target.value);
    this.setState({
      tipeTransaksi: e.target.value,
    });
    switch (e.target.value) {
      case "PEMBAYARAN LM":
        break;
      case "PEMBELIAN LM":
        break;
      case "PEMBAYARAN PER BON":
        console.log("MASUK PEMBAYARAN PER BON");
        Axios.get(serverNonMaster + "cst/dc/pjl/heads?statusValid=VALID")
          .then((res) =>
            this.setState({
              listBarang: res.data,
            })
          )
          .catch(() => gagal("Ambil data gagal, Mohon periksa koneksi anda"));
        break;
      default:
        break;
    }
  }
  render() {
    return (
      <>
        <div className="col-lg-12 col-md-12">
          <Stepper
            steps={[
              { title: "Pilih Faktur" },
              { title: "Pilihan Faktur" },
              { title: "Pembayaran" },
              // { title: "Data Rekening" },
            ]}
            activeStep={this.state.step}
          />
        </div>
        <br />
        <div className={this.state.step1}>
          <div className="col-lg-4">
            <label htmlFor="">Jenis Pembayaran</label>
            <Field
              name="jenisTransaksi"
              className="form-control"
              component="select"
              onChange={(e) => this.setTipeTransaksi(e)}
            >
              <option value="DEFAUL" key="DEFAUL">
                SILAHKAN PILIH JENIS PEMBAYARAN
              </option>
              <option value="PEMBAYARAN LM" key="PEMBAYARAN LM">
                PEMBAYARAN LM
              </option>
              <option value="PEMBELIAN LM" key="PEMBELIAN LM">
                PEMBELIAN LM
              </option>
              <option value="PEMBAYARAN PER BON" key="PEMBAYARAN PER BON">
                PEMBAYARAN PER BON
              </option>
            </Field>
          </div>
          <div className="col-lg-4 col-md-4">
            <label htmlFor="">Nama Toko</label>
            <SelectSearch
              options={this.state.listCustomer.map((obj) => {
                var payload = {
                  value: obj.namaToko + "|" + obj.namaCustomer,
                  name: obj.namaToko,
                };
                return payload;
              })}
              search
              placeholder="Silahkan Pilih Toko"
              onChange={(data) => this.PilihToko(data)}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Nomor Bon</label>
            <input
              type="text"
              className="form-control"
              onChange={this.onChange("noBon")}
            />
          </div>
          <div className="col-lg-12">
            {this.state.listBarang ? (
              <ToolkitProvider
                keyField="id"
                data={this.state.listBarang}
                columns={this.state.columns}
                search
              >
                {(props) => (
                  <div className="form-group">
                    <div className="text-right mt-5 mb-3">
                      <SearchBar
                        {...props.searchProps}
                        className="form-control"
                        placeholder="Pencarian"
                        style={{ margin: 0, top: "50%" }}
                      />
                    </div>
                    <BootstrapTable
                      {...props.baseProps}
                      striped
                      hover
                      condensed
                      bordered={true}
                      headerClasses="header-table"
                      pagination={paginationFactory()}
                    />
                  </div>
                )}
              </ToolkitProvider>
            ) : (
                <Skeleton width={"100%"} height={200} />
              )}
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="button"
                onClick={() => this.nextStep1()}
              >
                Next
              </button>
            </div>
          </div>
        </div>
        <div className={this.state.step2}>
          <div className="col-lg-12 mt-3">
            {this.props.selectedData ? (
              <ToolkitProvider
                keyField="id"
                data={this.props.selectedData}
                columns={this.state.pilihanColumns}
                search
              >
                {(props) => (
                  <div className="form-group">
                    <BootstrapTable
                      {...props.baseProps}
                      striped
                      hover
                      condensed
                      bordered={true}
                      headerClasses="header-table"
                      pagination={paginationFactory()}
                    />
                  </div>
                )}
              </ToolkitProvider>
            ) : (
                <Skeleton width={"100%"} height={50} count={5} className="mb-1" />
              )}
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="row">
              <div className="col-lg-6">
                <div className="text-left">
                  <button
                    className="btn btn-primary"
                    type="button"
                    onClick={() => this.prevStep()}
                  >
                    Back
                  </button>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="button"
                    onClick={() => this.nextStep()}
                  >
                    Next
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className={this.state.step3}>
          <div className="col-lg-12">
              <ul className="nav nav-pills bg-nav-pills nav-justified mb-3">
                <li className="nav-item">
                  <a
                    href="#rupiah"
                    data-toggle="tab"
                    aria-expanded="false"
                    className="nav-link rounded-0 active"
                    onClick={() =>
                      this.setState({
                        tipeTransaksi: "BARANG",
                      })
                    }
                  >
                    <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                    <span className="d-none d-lg-block">Gram</span>
                  </a>
                </li>
                <li className="nav-item">
                  <a
                    href="#gram"
                    data-toggle="tab"
                    aria-expanded="true"
                    className="nav-link rounded-0 "
                    onClick={() =>
                      this.setState({
                        tipeTransaksi: "RUPIAH",
                      })
                    }
                  >
                    <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                    <span className="d-none d-lg-block">Rupiah</span>
                  </a>
                </li>
              </ul>

              <div className="tab-content">
                <div className="tab-pane show active" id="rupiah">
                  <Suspense fallback={<Skeleton width={1134} height={200} />}>
                    <FormBayarPerBon
                      from="rupiah"
                      tipeTransaksi="Per Bon"
                      noFaktur={this.props.noFaktur}
                      totalCiok={this.props.totalCiok}
                      totalRongsok={this.props.totalRongsok}
                      totalBrutoCiok={this.props.totalBrutoCiok}
                      totalBrutoRongsok={this.props.totalBrutoRongsok}
                      onSubmit={(data) => this.handleSubmitGr(data)}
                    />
                  </Suspense>
                </div>
                <div className="tab-pane" id="gram">
                  <Suspense fallback={<Skeleton width={1134} height={200} />}>
                    <FormBayarPerBon
                      from="gram"
                      noFaktur={this.props.noFaktur}
                      totalCiok={this.props.totalCiok}
                      totalRongsok={this.props.totalRongsok}
                      totalBrutoCiok={this.props.totalBrutoCiok}
                      totalBrutoRongsok={this.props.totalBrutoRongsok}
                      onSubmit={(data) => this.handleSubmitRp(data)}
                    />
                  </Suspense>
                </div>
              </div>
          </div>
        </div>
      </>
    );
  }
}

FormPembayaranPerBon = reduxForm({
  form: "FormPembayaranPerBon",
  enableReinitialize: true,
})(FormPembayaranPerBon);
const selector = formValueSelector("FormPembayaranPerBon"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer, discount, persenan, discountGr } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "discount",
    "persenan",
    "discountGr"
  );

  var bayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf"))
    ? JSON.parse(localStorage.getItem("alatBayarTrf"))
    : 0;
  var bayarTunai = JSON.parse(localStorage.getItem("alatBayarTunai"))
    ? JSON.parse(localStorage.getItem("alatBayarTunai"))
    : 0;
  var totalCiok = JSON.parse(localStorage.getItem("totalCiok"))
    ? JSON.parse(localStorage.getItem("totalCiok"))
    : 0;
  var totalRosok = JSON.parse(localStorage.getItem("totalRongsok"))
    ? JSON.parse(localStorage.getItem("totalRongsok"))
    : 0;
  var totalGiro = JSON.parse(localStorage.getItem("totalGiro"))
    ? JSON.parse(localStorage.getItem("totalGiro"))
    : 0;
  var tunai = bayarTunai[0] === undefined ? 0 : bayarTunai[0].tunaiNetto;
  var transferr = bayarTrf[0] === undefined ? 0 : bayarTrf[0].transferNetto;
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    discountGr: (parseFloat(discount) * (parseFloat(persenan) / 100)).toFixed(
      3
    ),
    totalBayar: (
      parseFloat(tunai) +
      parseFloat(transferr) +
      parseFloat(totalCiok) +
      parseFloat(totalRosok) +
      parseFloat(totalGiro) +
      parseFloat(discountGr === "NaN" ? 0 : discountGr)
    ).toFixed(3),
  };
})(FormPembayaranPerBon);
